import React, { useState, useEffect } from "react"
import Heading from "../../common/Heading"
import "./hero.css"
import PropertyFilter from "../../common/PropertyFilter"
import { list } from "../../data/Data"
import Select from "react-select";

/* global BigInt */


const Hero = ({ onSearch }) => {
  const [location, setLocation] = useState("")
  const [selectedTypes, setSelectedTypes] = useState([])
  const [maxPrice, setMaxPrice] = useState("")
  const [availableTypes, setAvailableTypes] = useState([])

  useEffect(() => {
    // Extrair tipos nicos do CSV
    const uniqueTypes = [...new Set(list.map(item => item.type))].filter(Boolean)
    setAvailableTypes(uniqueTypes)
  }, [])

  const options = availableTypes.map(type => ({
  value: type,
  label: type,
}));

  const formatBRL = (digits) => {
  if (!digits) return ""
  const n = BigInt(digits || 0)
  const cents = Number(n % 100n)
  const units = Number(n / 100n)
  return new Intl.NumberFormat("pt-BR", { style: "currency", currency: "BRL" }).format(units + cents / 100)
}

  const unformatDigits = (formatted) => {
    return (formatted || "").replace(/\D/g, "")
  }

  const handlePriceChange = (e) => {
    const digits = unformatDigits(e.target.value)
    const limited = digits.slice(0, 12)
    setMaxPrice(limited)
  }

  const handleTypeChange = (e) => {
    const options = e.target.selectedOptions
    const types = Array.from(options, option => option.value)
    setSelectedTypes(types)
  }

  const submit = (e) => {
    e.preventDefault()
    // Converter maxPrice (dgitos) para nmero com centavos corretos
    const priceAsNumber = maxPrice ? parseFloat(maxPrice) / 100 : ""
    onSearch({ location, type: selectedTypes, maxPrice: priceAsNumber })
  }

  return (
    <>
      <section className='hero'>
        <div className='container'>
          <Heading 
          title='Encontre seu prximo investimento' 
          subtitle='Descubra as melhores oportunidades de imveis na sua cidade.' />
          <div className='hero-form'>
          <form className='flex' onSubmit={submit}>
            <div className='box'>
              <span>Localizao</span>
              <input value={location} onChange={(e) => setLocation(e.target.value)} type='text' placeholder='cidade, bairro ou rua' />
            </div>
            <div className='box'>
              <span>Tipo de imvel</span>
              <Select
                options={options}
                isMulti
                placeholder="Tipo de imvel"
                onChange={(selected) =>
                  setSelectedTypes(selected.map(item => item.value))
                }
              />
            </div>
            <div className='box'>
              <span>Valor Mximo (R$)</span>
              <input value={formatBRL(maxPrice)} onChange={handlePriceChange} type='text' placeholder='Ex: R$ 200.000,00' />
            </div>
            <div style={{margin: "3%"}}>
            <button className='btn1' type='submit'>
              <i className='fa fa-search'></i>
            </button>
            </div>
          </form>
          </div>
        </div>
      </section>
    </>
  )
}

export default Hero
