import React, { useEffect, useState } from "react"
import "./properties-map.css"

const PropertiesMap = ({ properties = [] }) => {
  const [groupedByAddress, setGroupedByAddress] = useState([])

  useEffect(() => {
    // Group properties by address and count
    const grouped = {}
    properties.forEach((prop) => {
      const addr = prop.location || "Endereo desconhecido"
      if (!grouped[addr]) {
        grouped[addr] = {
          address: addr,
          count: 0,
          items: [],
        }
      }
      grouped[addr].count += 1
      grouped[addr].items.push(prop)
    })

    const result = Object.values(grouped).sort((a, b) => b.count - a.count)
    setGroupedByAddress(result)
  }, [properties])

  const mapsKey = process.env.REACT_APP_GOOGLE_MAPS_API_KEY
  const centerAddress = properties.length > 0 ? (properties[0].location || "Brazil") : "Brazil"

  // Fallback map center query
  const mapQuery = encodeURIComponent(centerAddress)
  const mapSrc = mapsKey
    ? `https://www.google.com/maps/embed/v1/place?key=${mapsKey}&q=${mapQuery}`
    : `https://www.google.com/maps?q=${mapQuery}&output=embed`

  return (
    <div className='properties-map-container'>
      <div className='map-wrapper'>
        <iframe
          title='Mapa de Imveis'
          width='100%'
          height='600'
          style={{ border: 0, borderRadius: "8px" }}
          src={mapSrc}
          allowFullScreen=''
          loading='lazy'
        ></iframe>
      </div>

      <div className='addresses-list'>
        <h3>Localidades com Imveis ({groupedByAddress.length})</h3>
        {groupedByAddress.length === 0 ? (
          <p className='empty-message'>Nenhum imvel encontrado</p>
        ) : (
          <div className='addresses-scroll'>
            {groupedByAddress.map((group, idx) => (
              <div className='address-item' key={idx}>
                <div className='address-header'>
                  <i className='fa fa-map-marker-alt'></i>
                  <span className='address-name'>{group.address}</span>
                  {group.count > 1 && <span className='address-count badge'>{group.count}</span>}
                </div>
                {group.count > 1 && (
                  <div className='address-items'>
                    {group.items.map((item, i) => (
                      <div className='address-item-detail' key={i}>
                        <span className='item-name'>{item.name}</span>
                        <span className='item-price'>{item.price}</span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}

export default PropertiesMap
